@use "~@infineon/design-system-tokens/dist/tokens";


.col {
  display: flex;
  flex-direction: column;
  gap: 12px;
  color: tokens.$color-text-black;

  & ::slotted([slot=title]) {
    box-sizing: border-box;
    font-weight: 600;
    font-size: 16px;
    line-height: 24px;
    margin: 0;
    padding: 0;
  }

  & ::slotted([slot=link]) {
    box-sizing: border-box;
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
  }
}